import { Html, OrbitControls } from '@react-three/drei';
import React, { useState } from 'react';
import { Canvas , } from 'react-three-fiber';
import * as THREE from 'three';
import styled from 'styled-components';
import ThreeAlbum from '../components/ThreeAlbum';
import ThreeShadow from '../components/ThreeShadow'
import { Link } from 'react-router-dom';

const ThreeBlock = styled.div`
.pannel{
  position:absolute; top:0%; right:0%; font-size:1em; padding:30px;
 
}

`;

interface ThreeProps {

  spotifyTok: string | undefined;
 

};



const Three: React.FC<ThreeProps> = (props) => {
  const [albumInfo, setAlbumInfo] = useState<{artist:string|null , name:string|null}>({artist:null, name:null});

  
  return (
    <ThreeBlock>
      
      <Canvas style={{ width: '100vw', height: '100vh' }}>

        <ambientLight intensity={0.5} />
        <directionalLight intensity={0.8} position={[0, 0, 1]} />
        <pointLight position={[0, 1.5, 2]} />

        
        <ThreeAlbum spotifyTok={props.spotifyTok} setAlbumInfo={setAlbumInfo} />
        <ThreeShadow />
        
        
      </Canvas>
      
      <div className="pannel">
        <span className="artist"><Link to={''}>{albumInfo.artist}</Link></span> - <span className="album"><Link to={''}>{albumInfo.name}</Link></span>
      </div>
    </ThreeBlock>
  );
};

export default Three;