import React,{ useRef, useState} from 'react';
import styled from 'styled-components';
import { Albums } from '../routes/Main';
import { useNavigate } from 'react-router-dom';

const NewAlbumsBlock = styled.div`
    perspective:1000px;
    height:100vh; 
    position:relative;
    .container{
        display:flex; flex-wrap:wrap; justify-content:space-between; 
        margin:0 auto;
        width:63vw; 
        transform:rotateX(20deg);
        position:relative;
        .album{
            width:8vw; height:8vw; margin-bottom:1vw; position:relative;  
            .cover{
                z-index:999; width:100%; height:100%; 
                box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.5);
              
                -webkit-user-drag: none; 
                -moz-user-drag: none; 
                -ms-user-drag: none; 
                user-drag: none;
                
                -webkit-user-select: none;
                -moz-user-select: none; 
                -ms-user-select: none; 
                user-select: none;
                
                cursor:pointer;
                transform-origin: bottom center;

                .image{
                    width:100%; height:100%;  position:absolute;
                }
                .info{
                    font-family: 'Do Hyeon', sans-serif; font-size:1.5vw;
                    text-overflow: ellipsis; overflow: hidden;
                    white-space: nowrap; width:100%;
                    .title{width:100%; position:absolute; top:-9%;  text-overflow: ellipsis; overflow: hidden;
                        white-space: nowrap;}
                    .artist{width:100%; position:absolute; bottom:-9%;}
                }
            }
            
            .shadow{
                width:100%; height:100%;  position:absolute; background:white;
            }

        }

        .cancelArea{
            width:100%; height:100%;  position:fixed; top:0; left:0; z-index:999999;
        }
       
    }
    
    
`;

interface NewAlbumsProps {
    albums: Albums[];
}

const NewAlbums: React.FC<NewAlbumsProps> = ({albums}) => {
    const navigate = useNavigate();

    const [clickedCover, setClickedCover] = useState<string | null>();
    const [overedCover, setOveredCover] = useState<string | null>();
    const [cancelAreaOn, setCancelAreaOn] = useState<boolean>(false);
    
   
    const handleClickCover = (event: React.MouseEvent<HTMLDivElement>, albumId: string) => {
        setClickedCover(albumId);
        setOveredCover(null);
        setCancelAreaOn(true);
        if(clickedCover){
            navigate(`/three?album=${albumId}`)
        }

        /* navigate('/three'); */
    };
    
    const handleOverCover = ( imageId: string) => {
        setOveredCover(imageId);
    };
    const handleOutCover = () => {
        setOveredCover(null);
    };

    const handleCancelArea = () =>{
        setClickedCover(null);
        setCancelAreaOn(false);
    }

    return(
        <NewAlbumsBlock >

           
            <div  className="container" >
                <div className="cancelArea" style={{display:cancelAreaOn ? "block" : "none"}} onClick={handleCancelArea}></div>
                {albums?.map((data, index) => (
                    <div className="album" key={index}>
                        <div className="cover" onClick={(event) => handleClickCover(event, data.id)}
                            onMouseOver={() => handleOverCover(data.id)}
                            onMouseOut={() => handleOutCover()}
                            style={{
                                transform: overedCover === data.id ? `perspective(1000px) scaleY(1.1) rotateX(-23deg)` : "",
                                width: clickedCover === data.id ? "25vw" : "",
                                height: clickedCover === data.id ? "25vw" : "",
                                position: clickedCover === data.id ? "fixed" : "absolute",
                                left: clickedCover === data.id ? "50%":``,
                                top: clickedCover === data.id ? "50%":``,
                                marginTop: clickedCover === data.id ? "-12.5vw":"",     
                                marginLeft: clickedCover === data.id ? "-12.5vw":"",    
                                zIndex: clickedCover === data.id ? "999999999999" :"999",
                                filter: clickedCover === data.id || overedCover === data.id ? "grayscale(0%)" : "grayscale(80%)", 
                                opacity: clickedCover === data.id || overedCover === data.id ? "1" : cancelAreaOn ? "0.2" : "0.8",
                                transition: clickedCover === data.id ? "top 0.5s, left 0.5s , transform 0.3s" : "transform 0.3s"
                                }}
                            >

                            <img className="image"  src={data.images[1].url} alt="Album cover" 
                           
                            />

                            <div className="info" style={{display:clickedCover === data.id ?"block":"none"}}>
                                <div className="title">{data.name}</div>
                                <div className="artist">{data.artists[0].name}</div>
                            </div>

                        </div>
                        

                        <div className="shadow"></div>
                    </div>
                    
                ))}
            </div>

           
           
           
        </NewAlbumsBlock>
    );
}

export default NewAlbums;