import React, { useState, useEffect } from "react";
import { HashRouter as Router, Route, Routes } from "react-router-dom";
import axios from "axios";
import Main from "./routes/Main";
import Three from "./routes/Three";
import Header from "./components/Header";
import Footer from "./components/Footer";

const App = () => {
  const [spotifyTok, setSpotifyTok] = useState<string | undefined>();

  const [limit, setLimit] = useState<number | undefined>();

  useEffect(() => {
    

    fetchSportifyToken();

    handleNumberOfCover();
  }, []);

  const fetchSportifyToken = async () => {
    try {
      const client_id = '13a9dc50a95440468449ac29a9d21053';
      const client_secret = '055bd63bac66473ba4d2d073169a7a4e';

      const authOptions = {
        url: 'https://accounts.spotify.com/api/token',
        headers: {
          'Authorization': 'Basic ' + btoa(client_id + ':' + client_secret)
        },
        data: 'grant_type=client_credentials'
      };

      const response = await axios.post(authOptions.url, authOptions.data, {
        headers: authOptions.headers
      });

      if (response.status === 200) {
        const token = response.data.access_token;
        setSpotifyTok(token);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleNumberOfCover = () => {
    const hundredVh = window.innerHeight;
    const hundredVw = window.innerWidth;
    const marginBottom = hundredVw / 100;
    const coverHeight = (hundredVw / 100)*8;
    const calc = hundredVh / (marginBottom + coverHeight) ;
    const result = Math.floor(calc);
    setLimit(result * 7);
  };

  return (
   
    <Router>
      <Header />
      <Routes>
        
        <Route path="/" element={<Main limit={limit} spotifyTok={spotifyTok} />} />
        <Route path="/three" element={<Three spotifyTok={spotifyTok} />} />
  
        
      </Routes>
      <Footer />
    </Router>
  );
}

export default App;