import React,{useEffect, useRef, useState} from 'react';
import { useThree ,ThreeEvent ,useFrame } from 'react-three-fiber';
import { useSearchParams } from 'react-router-dom';
import axios from 'axios';
import * as THREE from 'three';
import { Group, KeyframeTrack } from 'three';
import ColorThief from "color-thief-ts";
import { Html } from '@react-three/drei';
import { Block } from '@react-three/fiber/dist/declarations/src/core/utils';


interface ThreeAlbumProps {

  spotifyTok: string | undefined;
  setAlbumInfo: React.Dispatch<React.SetStateAction<{ artist: string | null; name: string | null; }>>;

};




const ThreeAlbum: React.FC<ThreeAlbumProps> = (props) => {

  const albumRef = useRef<Group | null>(null);
  const frontAlbumRef = useRef<Group | null>(null);
  const textureRef = useRef<THREE.Texture | null>(null);
  const [searchParams] = useSearchParams();
  const albumId = searchParams.get('album');
  const [album,setAlbum]= useState<any>([]);
 
  const { viewport } = useThree();
  
  const { width, height } = viewport;

  const colorThief = new ColorThief();
  const [mainColor, setMainColor] = useState<string | null>(null);
  const [textColor, setTextColor] = useState<string | null>(null);


  const getAlbum =async () =>{
    const response = await axios.get(`https://api.spotify.com/v1/albums/${albumId}`, {
      headers: {
        'Authorization': `Bearer ${props.spotifyTok}`
      }
    });
    setAlbum(response.data)
    const textureLoader = new THREE.TextureLoader();
    const url = response.data.images[0].url
    const texture = textureLoader.load(url);
    textureRef.current = texture;
    getColor(url)
    
   props.setAlbumInfo({artist:response.data.artists[0].name,name:response.data.name})
  }

  useEffect(() => {

    getAlbum()
    
  }, []);

  const getColor = async (img: string) => {
    const dominantColor = await colorThief.getColorAsync(img)
    const hexColor = dominantColor.toString();
    setMainColor(hexColor)
    
    const r = parseInt(hexColor.substr(1, 2), 16);
    const g = parseInt(hexColor.substr(3, 2), 16);
    const b = parseInt(hexColor.substr(5, 2), 16);

  //   
    const brightness = ((r * 299) + (g * 587) + (b * 114)) / 1000;

    //      
    if (brightness > 125) {
      setTextColor("#000000")
    } else {
      setTextColor("#FFFFFF")
    }
  
    
  };
  
  
 
 
  useEffect(() => {
    if (albumRef.current) {
      const newSize = new THREE.Vector3(width * 0.3, width * 0.3, width * 0.3);
      albumRef.current.scale.copy(newSize);
 
    }
  }, []);
  
  const [isOpen ,setOpen] = useState<boolean>(false);
  const [isClose ,setClose] = useState<boolean>(false);
 

  // open animation
  useFrame(() => {
    if(frontAlbumRef.current && albumRef.current){
      const rotationSpeed = 0.1;
      
      if (isOpen) {
  
        frontAlbumRef.current.rotation.y += -rotationSpeed;
        albumRef.current.position.x += 0.067; // X  
        setas(false)
    
        if (frontAlbumRef.current.rotation.y <= -Math.PI) {
          frontAlbumRef.current.rotation.y=-Math.PI
          setOpen(false);
     
          setas(true)
        }
      } else if(isClose){

        frontAlbumRef.current.rotation.y += rotationSpeed;
        albumRef.current.position.x += -0.067;
        setas(false)
        if (frontAlbumRef.current.rotation.y >= 0) {
          frontAlbumRef.current.rotation.y=0
          setClose(false);
    
          
        }
      }
    }
    
  });

  const [as,setas]=useState<boolean>(false)

  
  const handleClick = (event: ThreeEvent<MouseEvent>) =>{
    event.stopPropagation(); //    
    
    if(frontAlbumRef.current){
      if(frontAlbumRef.current.rotation.y >= 0){
        setOpen(true);
      } else if(frontAlbumRef.current.rotation.y <= -Math.PI){
        setClose(true);
      
      }
    }
  }

  useFrame(({ mouse }) => {
    if (albumRef.current && frontAlbumRef.current) {
      if(frontAlbumRef.current.rotation.y === 0){
        albumRef.current.rotation.y = mouse.x * 0.5;
        albumRef.current.rotation.x = mouse.y * 0.5;
      }else {
        albumRef.current.rotation.y = 0;
        albumRef.current.rotation.x = 0;
      }
      
    }
  });

  const [trackHovered, setTrackHovered] = useState<string| null>(null);

  const handleMouseEnter = (itemId:string) => {
    setTrackHovered(itemId);
  };

  const handleMouseLeave = () => {
    setTrackHovered(null);
  };

  

  return (
 
    <group ref={albumRef} name="album" onClick={handleClick}>
      <group name="backAlbum">
        <mesh name="backBoard" position={[0, 0, 0]}>
          <boxGeometry args={[1, 0.95, 0.05, 30, 5]} />
          <meshPhysicalMaterial
            color={0xffffff}
            transparent={true}
            opacity={0.3}
            roughness={0.4}
            metalness={0.1}
            envMapIntensity={1}
          />
          <lineSegments>
            <edgesGeometry args={[new THREE.BoxGeometry(1, 0.95, 0.05,30,5)]} />
            <lineBasicMaterial color={0xcfcccc} />
          </lineSegments>
        </mesh>
        <group position={[-0.55, 0, 0.025]}>
          <mesh name="hingeBoard" >
              <boxGeometry args={[0.1, 0.95, 0.1]} />
              <meshPhysicalMaterial
                color={0xffffff}
                transparent={true}
                opacity={0.3}
                roughness={0.4} 
                metalness={0.1}
                envMapIntensity={1}
              />
              <lineSegments>
                <edgesGeometry args={[new THREE.BoxGeometry(0.1, 0.95, 0.1)]} />
                <lineBasicMaterial color={0xcfcccc} />
              </lineSegments>
          </mesh>
          <mesh name="hingePaper" position={[0, 0, 0]}>
            <boxGeometry args={[0.09, 0.93, 0.09]} />
            <meshBasicMaterial color={`${mainColor}`} />
            
          </mesh>
          
        </group>
        

        <group name="cdBoard" position={[0, 0, -0.001]}>
          <mesh >
            <ringGeometry args={[0.15, 0.47, 32]} />
            <meshPhysicalMaterial
              color={0xffffff}
              emissive={0x000000}
              roughness={0.4}
              metalness={0.5}
              side={THREE.BackSide}
            />
          </mesh>
          <mesh>
            <ringGeometry args={[0.15, 0.47, 32]} />
            <meshBasicMaterial map={textureRef.current}/>
          </mesh>
          <mesh>
            <ringGeometry args={[0.075, 0.15, 32]} />
            <meshPhysicalMaterial
              transmission={0.95}
              thickness={0}
              roughness={0.2}
              side={THREE.BackSide}
            />
            <lineSegments>
            <edgesGeometry args={[new THREE.RingGeometry( 0.075, 0.15, 32 )]} />
            <lineBasicMaterial color={0xe5e5e5} />
          </lineSegments>
          </mesh>
        </group>

      </group>
      
     
      <group ref={frontAlbumRef} name="frontAlbum" position={[-0.55, 0, 0.025]} >
      
        <mesh name="frontBoard" position={[0.55, 0, 0.025]}>
          <boxGeometry args={[1, 0.95, 0.05]} />
          <meshPhysicalMaterial
            color={0xffffff}
            transparent={true}
            opacity={0.3}
            roughness={0.4}
            metalness={0.1}
            envMapIntensity={1}
          />
          <lineSegments>
            <edgesGeometry args={[new THREE.BoxGeometry(1, 0.95, 0.05)]} />
            <lineBasicMaterial color={0xcfcccc} />
          </lineSegments>
          
          {as === true ? 
            <Html transform rotation={[0,Math.PI,0]} scale={[0.1,0.1,0.1]}>
              <div style={{ width:"350px",height:"350px", fontSize:"15px" , display:"flex",justifyContent:"space-evenly", flexDirection:"column", alignItems:"center"}}>
                {album.tracks.items.map((track:any)=>(
                  <div  onMouseEnter={() => handleMouseEnter(track.id)}
                  onMouseLeave={()=>handleMouseLeave()} key={track.id} style={{ width:"100%", textAlign:"center", overflow: "hidden", textOverflow: "ellipsis", whiteSpace: "nowrap", cursor:"pointer",  textDecoration: trackHovered === track.id ? 'underline' : ''}}>
                    {track.name}
                  </div>
                ))}
              </div>
              </Html>
          :null}
          
          
        </mesh>
      
      
       
        <mesh name="cover" position={[0.55, 0, 0.025]}>
          <boxGeometry args={[0.95, 0.9, 0.005]} />
          
        
          <meshBasicMaterial attach="material-0" color={`${mainColor}`} />
          
          <meshBasicMaterial attach="material-1" color={`${mainColor}`} />
         
          <meshBasicMaterial attach="material-2" color={`${mainColor}`} />
          <meshBasicMaterial attach="material-3" color={`${mainColor}`} />
       
          <meshBasicMaterial attach="material-4" map={textureRef.current} />
          
          <meshBasicMaterial attach="material-5" color={`${mainColor}`} />
         
          
        </mesh>
       
       
      </group>
    
  </group>

  );
};

export default ThreeAlbum;