import React,{useEffect, useState} from 'react';
import styled from 'styled-components';
import axios from 'axios';
import Header from '../components/Header';
import NewAlbums from '../components/NewAlbums';
import Footer from '../components/Footer';


const MainBlock = styled.div`
 
`;

interface MainProps {

  spotifyTok: string | undefined;
  limit: number | undefined;

};

export interface Albums {
  artists: {
    name: string;
    id: string;
  }[];
  id: string;
  images: {
    url: string;
  }[];
  name: string;
  release_date: string;
}



const Main: React.FC<MainProps> = (props) => {

  const [albums, setAlbums] = useState<Albums[]>([]);

  


  useEffect(()=>{
    
    if(props.limit !== undefined && props.spotifyTok !== undefined){
      if(props.limit <= 50){
        fetchNewReleases(props.limit,0);
      } else if(props.limit > 50){
        // for next fetch over the limit
        fetchNewReleases(50,0);
      }
    }
    
     
  },[props.spotifyTok, props.limit])


  const fetchNewReleases = async (limit: number , offset: number) => {
  
   
    try {
      const response = await axios.get(`https://api.spotify.com/v1/browse/new-releases?country=KR&limit=${limit}${offset !== 0 ? `&offset=${offset}` : ''}`, {
        headers: {
          'Authorization': `Bearer ${props.spotifyTok}`
        }
      });

      setAlbums((prevAlbums) => [...prevAlbums, ...response.data.albums.items]);
      if (props.limit !== undefined && props.limit > 50) {
        const remainingAlbums = props.limit - 50;
        fetchNewReleases(remainingAlbums,limit)
      }
    
    } catch (error) {
      console.error('Error fetching new releases:', error);
    }
  };

  return (
    <MainBlock>
      
      <NewAlbums albums={albums}/>
     
    </MainBlock>
  );
};

export default Main;